.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  min-height: auto;
  display: flex;
  justify-content: flex-start;
  padding: 2rem 2rem 0 2rem;
  align-items: flex-start;
  width: 100%;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.App-header h1 {
  font-family: 'Lato', sans-serif;
  font-size: 2.5rem;
  margin: 0;
  color: #282c34;
}

.header-container {
  display: flex;
  align-items: center;
  width: 100%;
  white-space: nowrap;
}

.tab-menu {
  margin-left: auto;
  margin-right: 5%;
}

.tab-menu ul {
  display: flex;
  list-style: none;
  margin: 0;
  padding: 0;
  gap: 4vw;
  white-space: nowrap;
}

.tab-menu a {
  font-family: 'Lato', sans-serif;
  font-size: 1.1rem;
  color: #666;
  text-decoration: none;
  transition: color 0.2s ease;
}

.tab-menu a:hover {
  color: #282c34;
}

.content-container {
  display: flex;
  width: 100%;
  padding: 0;
  gap: 2rem;
  align-items: center;
}

.profile-image {
  width: 33%;
  padding: 2rem 2rem 0 4rem;
  display: flex;
  justify-content: center;
}

.profile-image img {
  width: 95%;
  height: auto;
  object-fit: cover;
  border-radius: 20px;
}

.video-container {
  width: 27%;
  padding: 2rem;
  display: flex;
  justify-content: center;
  overflow: hidden;
}

.video-wrapper {
  width: 100%;
  position: relative;
  padding: 177.78% 0 0 0;
  overflow: hidden;
}

.video-wrapper::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 10px;
  z-index: 1;
  pointer-events: none;
}

.video-wrapper iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 0px;
  z-index: 0;
}

.social-links {
  margin-top: 1rem;
  display: flex;
  gap: 1rem;
  justify-content: flex-start;
}

.icon-button {
  background: none;
  border: none;
  font-size: 1.8rem;
  color: #666;
  cursor: pointer;
  transition: color 0.2s ease;
  padding: 0;
}

.icon-button:hover {
  color: #282c34;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content.resume-modal {
  background-color: white;
  padding: 2rem;
  border-radius: 10px;
  width: 90%;
  max-width: 1000px;
  max-height: 90vh;
  position: relative;
  overflow-y: auto;
}

.resume-content {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}

.close-button {
  position: absolute;
  top: 1rem;
  right: 1rem;
  background: none;
  border: none;
  font-size: 1.5rem;
  cursor: pointer;
  color: #666;
  z-index: 1001;
}

.close-button:hover {
  color: #282c34;
}

.tab-button {
  background: none;
  border: none;
  font-family: 'Lato', sans-serif;
  font-size: 1.1rem;
  color: #666;
  cursor: pointer;
  transition: color 0.2s ease;
  padding: 0.5rem 0;
  position: relative;
}

.tab-button:hover {
  color: #282c34;
}

.tab-button.active {
  color: #282c34;
}

.tab-button.active::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #282c34;
}

@media (max-width: 1024px) {
  .content-container {
    grid-template-columns: 1fr;
    gap: 1rem;
  }

  .profile-image,
  .video-container,
  .bio-section {
    grid-column: 1;
  }

  .bio-section {
    padding-right: 0;
  }
}

.right-section {
  flex: 1;
  padding: 2rem;
  display: flex;
  align-items: center;
}

.text-content {
  max-width: 600px;
}

.icon-link {
  color: #333;
  font-size: 1.5rem;
  transition: color 0.3s ease;
}

.icon-link:hover {
  color: #666;
}

/* Add styles for react-pdf */
.react-pdf__Document {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}

.react-pdf__Page {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
  margin-bottom: 1rem;
}

.page-controls {
  margin-top: 1rem;
  display: flex;
  gap: 1rem;
  align-items: center;
}

.page-controls button {
  padding: 0.5rem 1rem;
  background-color: #f5f5f5;
  border: 1px solid #ddd;
  border-radius: 4px;
  cursor: pointer;
}

.page-controls button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Mobile-specific styles */
@media (max-width: 768px) {
  .App-header {
    padding: 1rem;
  }

  .header-container {
    flex-direction: column;
    align-items: flex-start;
    white-space: normal;
  }

  .tab-menu {
    width: 100%;
    margin: 1rem 0;
  }

  .tab-menu ul {
    gap: 1rem;
    flex-wrap: wrap;
    white-space: normal;
  }

  .content-container {
    flex-direction: column;
    padding: 1rem;
  }

  .video-container {
    width: 100%;
    padding: 0;
    margin-bottom: 1rem;
  }

  .video-wrapper {
    position: relative;
    padding-top: 177.78%;
    height: 0;
    overflow: hidden;
  }

  .video-wrapper iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }

  .bio-section {
    width: 100%;
    padding: 1rem;
  }
}

.bio-section {
  width: 73%;
  padding: 2rem;
  text-align: left;
  display: flex;
  align-items: center;
  height: 100%;
}

.bio-content {
  width: 100%;
}

.bio-section h2 {
  font-family: 'Lato', sans-serif;
  font-size: 1.8rem;
  color: #282c34;
  margin-top: 0;
  margin-bottom: 1.5rem;
}

.bio-section p {
  font-family: 'Lato', sans-serif;
  font-size: 1.2rem;
  line-height: 1.7;
  color: #666;
}

.learn-more-link {
  text-decoration: underline;
  cursor: pointer;
  color: #666;
  transition: color 0.2s ease;
}

.learn-more-link:hover {
  color: #282c34;
}
